.not_found_image {
  margin-top: 50px;
  background-image: url('https://c4.wallpaperflare.com/wallpaper/495/393/162/deadpool-desktop-hd-wallpaper-preview.jpg');
  background-repeat: no-repeat;
  background-size: contain;
  width: 400px;
  height: 300px;
}
.not_found_wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.not_found_text {
  display: flex;
  align-items: center;
}
