.footer {
  width: 100%;
  height: 65px;
  bottom: 0;
  color: #ece8a9;
  background-color: rgb(44, 56, 88);
  display: flex;
  justify-content: space-around;
  align-items: center;
  cursor: default;
}

.footer_span {
  display: flex;
  justify-content: center;
  align-items: center;
}

.author {
  margin-left: 5px;
}

.footer_icon {
  color: crimson;
  font-size: 24px;
  margin-left: 5px;
  animation-name: pulse;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.3);
  }

  100% {
    transform: scale(1);
  }
}

@media (max-width: 768px) {
  .footer {
    width: 870px;
    height: 75px;
    font-size: 1.3rem;
    justify-content: center;
  }
}